


























import { Vue, Component, Prop, Emit, Model, Watch } from 'vue-property-decorator';

@Component({
    components: {},
})
export class SearchInput extends Vue {
    // Prop
    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private placeholder: string;

    // Model
    @Model('input', {
        type: String,
        default: () => undefined,
    })
    private value: string;

    @Watch('modelData', { immediate: true })
    private onVisibleChanged(newval: boolean, oldval: boolean): void {
        this.$emit('input', this.modelData);
    }

    private modelData: string = '';

    private created(): void {
        this.modelData = this.value;
    }

    private search(): void {
        this.$emit('search', this.modelData);
    }
}

export default SearchInput;
Vue.component('search-input', SearchInput);
